<template>
	<div class="forList" :style="f">
		<div class="topNav">
			<h4>
				<i @click="returns">&lt;</i><p>注册</p>
			</h4>
		</div>
		<div class="registration">
			<input type="text" placeholder="手机号/用户名/邮箱" />
			<input type="password" placeholder="密码" />
			<input type="number" placeholder="请输入手机验证码"/>
			<span>获取手机验证码</span>
			<p>
				<i class="fa fa-check-circle" :style="checkTwo" @click="check"></i>
				<span>我已同意 <a href="#">《网络协议》</a></span>
			</p>
			<input type="button" value="下一步"/>
		</div>
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				checkTwo:'color:#1d88eb',
				f:this.n
			}
		},
		methods:{
			check(){
				if(this.checkTwo=='color:#1d88eb'){
					this.checkTwo=""
				}else{
					this.checkTwo='color:#1d88eb'
				}
			},
			returns(){
				this.$emit("toggle")
			},
			props:["n","toggle"]
		}
	}
</script>

<style scoped>
	.forList{
		height: 22rem;
		background-color: white;
		font-size: .36rem;
		position: fixed;
		top: 0; 
		/*transform: translate(22rem);*/
		transition: .9s;
		z-index: 5;
	}
	.topNav{
		width: 11.52rem;
   		height: 1.92rem;
   		padding: 1px 0.45rem 0px 0.45rem;
   		color: white;
   		background: url(../../static/img/25.png) no-repeat;
   		background-size: cover;
   		font-size: .45rem;
		
	}
	.topNav>h4{
		margin-top: .8rem;
		font-weight: normal;
	}
	.topNav>h4>p{
		text-align: center;
	}
	.topNav>h4>i{
		float: left;
	}
	.registration{
		height: 6.08rem;
		padding: .72rem .44rem 0rem .47rem;
		position: relative;
	}
	.registration>input{
		width: 11.52rem;
		height: 1.49rem;
		border: none;
		outline: none;
		border-bottom: 1px solid #f0f0f0;
		font-size: .36rem;
	}
	.registrationinput::-webkit-input-placeholder {
		color: #d5d5d5;
	}
	.registration>input:nth-of-type(1){
		background-image: url(../../static/img/phone.png);
		background-repeat: no-repeat;
		background-position: 0.05rem 50%;
		background-size: .35rem .5rem;
		text-indent: 1rem;
	}
	.registration>input:nth-of-type(2){
		background-image: url(../../static/img/lock.png);
		background-repeat: no-repeat;
		background-position: 0.05rem 50%;
		background-size: .35rem .43rem;
		text-indent: 1rem;
	}
	.registration>input:nth-of-type(3){
		background-image: url(../../static/img/massage.png);
		background-repeat: no-repeat;
		background-position: 0.05rem 50%;
		background-size: .35rem .43rem;
		text-indent: 1rem;
	}
	.registration>input:nth-of-type(4){
		height: 1.34rem;
		margin-top: .8rem;
		background-color: #1d88eb;
		color: white;
	}
	.registration>span{
		position: absolute;
		top: 4.1rem;
		left: 8.4rem;
		color: #1d88eb;
	}
	.registration>p{
		margin-top: .52rem;
		font-size: .39rem;
	}
	.registration>p>i{
		/*color: #1d88eb;*/
	}
	.registration>p>span{
		margin-left: .3rem;
	}
	.registration>p>span>a{
		text-decoration: underline;
		color: #1d88eb;
	}
</style>


