<template>
	<div class="carList"  id="carList" v-carList  v-touch:swipe='play'>
		<h4>
			<img src="static/img/16.png" />
			<span>品牌介绍</span>
		</h4>
		<p>一汽-大众奥迪</p>
		<ul>
			<li>
				<img src="static/img/13.png"/>
				<p>奥迪A4L</p>
				<p>21.22-31.96</p>
			</li>
		</ul>
	</div>
</template>

<script>
	import Vue from 'vue'
	
	
//	Vue.directive('carList',function(el){
////		value.style.color="red"
////		console.log(value)
////		console.log(el)
////		console.log(ol)
