//			})
//			console.log(el)		
//	})
	
	export default{
		data(){
			return{
			}
		},
		directives:{
			carList:{
				bind:function(el,al,ve){
					el.addEventListener('touchend',function(ev){
						if(ev.changedTouches[0].pageX>500){
							ve.context.$emit("play")
						}
//						console.log(ev.changedTouches[0].pageX)
					})
				}
			}
		},
		methods:{
			play(el){
//				if(el=='swiperight'){
////					this.$emit("play")
//				}
				}
		}
	}
</script>

<style scoped>
	.carList{
		width: 10rem;
		height: 18.68rem;
		font-size: .45rem;
		/*padding-left: .49rem;*/
		background: #f2f2f2;
		border: 1px solid black;
	}
	.carList>h4{
		height: 1.8rem;
		padding-left: .47rem;
		background-color: white;
		margin-bottom: .3rem;
		padding-left: .49rem;
	}
	.carList>h4>img{
		width: 1.25rem;
		height: 1.8rem;
		vertical-align: middle;
	}
	.carList>h4>span{
		margin-left: 1.48rem;
	}
	.carList>p{
		height: .43rem;
		padding: .45rem 0px 0.16rem .49rem;
		/*margin-bottom: .16rem;*/
		background: white;
	}
	.carList>ul>li{
		padding: 1px;
		height: 2.12rem;
		border-bottom: 1px solid #f0f0f0;
		background: white;
		padding-left: .49rem;
	}
	.carList>ul>li>img{
		width: 3rem;
		height: 2.12rem;
		float: left;
	}
	.carList>ul>li>p{
		text-indent: .45rem;
	}
	.carList>ul>li>p:nth-of-type(1){
		margin-top: .52rem;
	}
	.carList>ul>li>p:nth-of-type(2){
		margin-top: .35rem;
		color: #ff4f53;
	}
</style>


