<template>
		<div class="footList">
    		<ul>
    			<li v-for="(i,index) in linto" ref='txt'>
    				<router-link class="routerLi" :to=i.links>
    						<i :class=i.className></i>
    						<p>{{i.info}}</p>
    				</router-link>
    			</li>
    		</ul>
    </div>
</template>

<script>
	export default{
		data(){
			return{
				linto:[
				{links:'/home',className:'fa fa-3x fa-home',info:'新车'},
				{links:'second',className:"fa fa-3x fa-refresh",info:'二手车'},
				{links:'third',className:"fa fa-3x fa-minus-circle",info:'发现'},
				{links:'fourth',className:"fa fa-3x fa-commenting-o",info:'帮买车'},
				{links:'fifthChild',className:"fa fa-3x fa-circle-thin",info:'我的'},
				
				]
			}
		},
		methods:{}
	}
</script>

<style scoped>
    	.footList{
    		height: 1.55rem;
    		background-color: white;
    	}
    	 .footList>ul>li{
    		width: 20%;
    		height: 1.32rem;
    		float: left;
    		text-align: center;
    		padding-top: .2rem;
    	}
    	 .footList>ul>li>.routerLi{
    			height: 1.32rem;
    			display: block;
    			font-size: .28rem;
    		}
    	 .footList>ul>li>.routerLi>p{
    			width: 100%;
    			text-align: center;
    		}
    	.router-link-active{
    		color: #4286e4;
    	}
</style>


