import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

axios.defaults.withCredentials = true;
var myAxios = axios.create({
	baseURL:'http://localhost:3000/'
});

var urls = window.location.href

const state ={
	userName:"账号登陆",
	info:"张三",
	password:123456
}

const mutations ={
	main(state,ss){
		console.log(urls,ss)
		
	}
}

export default new Vuex.Store({
	state,mutations
})


