<template>
	<div class="forList" :style="attribute">
		<div class="topNav">
			<h4>
				<p>账号登录<i @click="hidd">X</i></p>
			</h4>
		</div>
		<div class="registration">
			<input type="text" placeholder="手机号/用户名/邮箱" ref="name"/>
			<input type="password" placeholder="密码"  ref="pass"/>
			<input type="button" value="登录" @click="landfall" />
			<h4 :style="prompting">{{success}}</h4>
			<p>
				<span @click="next">立即注册</span>
				<span>忘记密码</span>
			</p>
		</div>
		<div class="thirdParty">
			<ul>
				<li>
					<img src="static/img/qq.png" />
					<p>QQ</p>
				</li>
				<li>
					<img src="static/img/wb.png" />
					<p>微博</p>
				</li>
				<li>
					<img src="static/img/wx.png" />
					<p>微信</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		methods:{
			hidd(){
				this.$emit("transmit")
			},
			next(){
				this.$emit("toggles")
			},
			play(){
				this.$emit("toggleA",this.$refs.name.value,"aa")
			},
			landfall(){
				if(this.$refs.name.value==this.user.name&&this.$refs.pass.value==this.user.password){
					this.prompting={opacity:"1",color:"green"};
					this.success="登陆成功！";
					var that = this;
					setTimeout(function(){
						that.prompting="opacity: 0";
						that.play();
					},1500);
					
				}else{
					this.prompting={opacity:"1",color:"red"};
					this.success="登陆失败！";
					var that = this;
					setTimeout(function(){
						that.prompting="opacity: 0"
					},1500)
				}
			}
		},
		data(){
			return{
				attribute:this.m,
				user:{name:"张三",password:123456},
				prompting:"opacity: 0",
				success:""
			}
		},
		
		
		props:["m","transmit",'toggle']
	}
</script>

<style scoped>
	.forList{
		height: 22rem;
		background-color: white;
		font-size: .36rem;
		position: fixed;
		top: 0; 
		transition: .9s;
		z-index: 4;
	}
	.topNav{
		width: 11.52rem;
   		height: 1.92rem;
   		padding: 1px 0.45rem 0px 0.45rem;
   		color: white;
   		background: url(../../static/img/25.png)no-repeat;
   		background-size: cover;
   		font-size: .45rem;
		
	}
	.topNav>h4{
		margin-top: .8rem;
		font-weight: normal;
	}
	.topNav>h4>p{
		text-align: center;
	}
	.topNav>h4>p>i{
		float: right;
	}
	.registration{
		height: 6.08rem;
		padding: .72rem .44rem 0rem .47rem;
		position: relative;
	}
	.registration>h4{
		width: 2rem;
		height: 1rem;
		line-height: 1rem;
		text-align: center;
		position: absolute;
		bottom: .5rem;
		left: 4.8rem;
		/*opacity: 0;*/
		transition: 1s;
	}
	.registration>input{
		width: 11.52rem;
		height: 1.49rem;
		border: none;
		outline: none;
		border-bottom: 1px solid #f0f0f0;
		font-size: .36rem;
	}
	.registrationinput::-webkit-input-placeholder {
		color: #d5d5d5;
	}
	.registration>input:nth-of-type(1){
		text-indent: 1rem;
		background-image: url(../../static/img/phone.png);
		background-repeat: no-repeat;
		background-position: 0.05rem 50%;
		background-size: .35rem .5rem;
	}
	.registration>input:nth-of-type(2){
		text-indent: 1rem;
		background-image: url(../../static/img/lock.png);
		background-repeat: no-repeat;
		background-position: 0.05rem 50%;
		background-size: .35rem .43rem;
	}
	.registration>input:nth-of-type(3){
		height: 1.34rem;
		background-color: #1d88eb;
		color: white;
	}
	.registration>p{
		margin-top: .52rem;
		font-size: .39rem;
		color: #1d88eb;
	}
	.registration>p>span:nth-of-type(1){
		float: left;
	}
	.registration>p>span:nth-of-type(2){
		float: right;
	}
	.thirdParty{
		height: 2.35rem;
		margin-top: 1.88rem;
		padding: 0rem .44rem 0rem .47rem;
	}
	.thirdParty>ul>li{
		width: 1.24rem;
		height: 100%;
		float: left;
	}
	.thirdParty>ul>li>img{
		width: 1.24rem;
		height: 1.24rem;
	}
	.thirdParty>ul>li>p{
		width: 1.24rem;
		height: 1.1rem;
		text-align: center;
		line-height: 1.1rem;
	}
	.thirdParty>ul>li:nth-of-type(1){
		margin-left: 1rem;
	}
	.thirdParty>ul>li:nth-of-type(2){
		margin-left: 2.9rem;
	}
	.thirdParty>ul>li:nth-of-type(3){
		margin-left: 2.9rem;
	}
</style>


